.container {
  margin: 30px auto;
  width: 80vw;
  min-height: 90vh;

  display: flex;
  flex-direction: column;
}

.headline {
  margin-bottom: 2rem;
  color: $grey-dark;
}

.employees {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 30px;
}
